import db from 'libs/db';
import React from 'react';
import ReactSignatureCanvas from 'react-signature-canvas';
import { toast } from 'react-toastify';
import ProgressBar from 'react-bootstrap/ProgressBar';
import AdminServices from 'services/api/AdminServices';
import cssVar from 'views/mobileView/styles';
import { Cloud } from 'assets/SVG';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { AiOutlineRight } from 'react-icons/ai';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

const SignUpload = (props) => {
    const taxStatus = props.location.state?.taxStatus

	const [imgSign, setImg] = React.useState('');
	const [signID, setsignID] = React.useState('');

	const [modalType, setmodalType] = React.useState('sign');
	const [loading, setloading] = React.useState(true);
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const [user, setUser] = React.useState({});
	const [userProfile, setUserProfile] = React.useState({});
	const [profiled,setProfiled] =React.useState(false)
	var inputFile = React.useRef(null)
	var sigCanvas = React.useRef(null)

	React.useEffect(() => {
		checkValidation();
	}, [signID]);

	function urltoFile(url, filename, mimeType) {
		return (fetch(url)
			.then(function (res) { return res.arrayBuffer(); })
			.then(function (buf) { return new File([buf], filename, { type: mimeType }); })
		);
	}

	let uploadImage = async (singleFile, modalType) => {
		//Check if any file is selected or not
		if (singleFile != null) {
			//If file selected then create FormData
			const fileToUpload = singleFile;
			const data = new FormData();
			data.append('file', fileToUpload);
			try {
				var res = await AdminServices.uploadDoc(user.id, data).then((res) => {
					return res;
				});
				console.log(res.data);
				if (res.type) {
					if (res.data.DocumentId) {
						setsignID(res.data.DocumentId);
					} else {
						console.log('ERR : 2', res.msg);
						toast.error("try again...")
					}
				} else {
					console.log('ERR : 1', res.msg);
					toast.error("try again...")

				}
			} catch (error) {
				console.log('ERR : c', error);
				toast.error("try again...")

			}
		} else {
			//if no file selected the show alert
			alert('Please Select File first');
		}
	};

	const imagePick = (event) => {
		setImg(URL.createObjectURL(event.target.files[0]));
		var file = event.target.files[0];

		uploadImage(file, 'sign');
	};



	const handleSignature = () => {
		var a = sigCanvas.getTrimmedCanvas().toDataURL('image/png')
		console.log(a)
		urltoFile(a, 'sign.png', 'image/png')
			.then(function (file) { uploadImage(file, 'sign'); });

	};

	const handleClear = () => {
		console.log('clear success!');
		sigCanvas.clear()
		setsignID('')
		setImg('');
	};

	const checkValidation = () => {
		if (signID !== '' && signID !== null) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true);
		}
	};

	function getDateFinal(dateString) {
		console.log(dateString)
		dateString = dateString.split(" / ").reverse().join("-");
		return dateString;
	}

	const getClientData = async (id) => {
		await db.get(db.model.clientData).then(async (p) => {
		  if (p === null) {
			let res = await AdminServices.getClient(id).then(res => res);
			if (res.type === true) {
			  const a = JSON.parse(res.data);
			  let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
			  console.log("Get Client API :", aa)
			  setProfiled(aa.Client[0].IsProfileComplete === 1)
			  db.set(db.model.clientData, JSON.stringify(aa))
			}
		  } else {
			setProfiled(p.Client[0].IsProfileComplete === 1)
		  }
		})
	  };

	const onNextPress = async () => {
		console.log(profiled)
		if(profiled){
			toast.error('Profile already completed');
			props.history.push('/dashboard')
			return;
		}
		var final = {
			client: {
				clientId: user.id,
				clientName: userProfile.name,
				mobileNumber: userProfile.taxStatus === '2' ? userProfile.PersonalDetails.phone : '',
				gender: userProfile.PersonalDetails.gender === '0' ? 1 : 2,
				dateOfBirth: new Date(getDateFinal(userProfile.PersonalDetails.dob)),
				pan: userProfile.taxStatus === '1' ? '' : userProfile.panData.pan,
				kycStatus: userProfile.taxStatus === '1' ? userProfile.GuardianPan.kycRegistered : userProfile.panData.kycRegistered,
				taxStatus: userProfile.taxStatus === '0' ? 1 : userProfile.taxStatus === '1' ? 2 : userProfile.BankDetails.type,
				guardianPAN: userProfile.taxStatus === '1' ? userProfile.GuardianPan.pan : '',
				guardianName: userProfile.taxStatus === '1' ? userProfile.GuardianName : '',
				guardianDOB: userProfile.taxStatus === '1' ? new Date(getDateFinal(userProfile.GuardianName)) : new Date('0000-00-00'),
				panFileId: userProfile.doc.panID,
				bankFileId: userProfile.doc.chequeID,
				signFileId: signID
			},
			addresses: [
				{
					clientId: user.id,
					address1: userProfile.AddressDetails.line1,
					address2: userProfile.AddressDetails.line2,
					city: userProfile.AddressDetails.city,
					state: userProfile.AddressDetails.state,
					pincode: userProfile.AddressDetails.pincode,
					countryId: userProfile.AddressDetails.country.CountryCode
				}
			],
			fatcas: [
				{
					clientId: user.id,
					pan: userProfile.taxStatus === '1' ? userProfile.GuardianPan.pan : userProfile.panData.pan,
					birthCountryId: 0,
					residentCountryId: 0,
					isTaxResident: userProfile.isTaxResident==='YES'?true:false,
					politicalExposure: userProfile.politicalExposure,
					annualIncomeSlab: Number(userProfile.Fatca.income.IncomeCode),
					sourceofWealth: Number(userProfile.Fatca.wealth.SourceCode),
					occupation: userProfile.Fatca.occupation.OccupationCode,
					identificationType: ''
				}
			],
			nominees: userProfile.taxStatus !== '1' && userProfile.Nominee?.addNominee === '0' ?
				[{
					clientId: user.id,
					name: userProfile.Nominee.noName,
					relation: userProfile.Nominee.noRelation,
					dateOfBirth: new Date(getDateFinal(userProfile.Nominee.noDob)),
					allocation: 100,
					guardianName: userProfile.Nominee.noGuardian
				}]
				:
				[]
			,
			bankInfo: {
				clientId: user.id,
				bankId: userProfile.BankDetails.UID,
				bankName: userProfile.BankDetails.name,
				accountType: userProfile.BankDetails.type,
				accountNumber: userProfile.BankDetails.number,
				ifsc: userProfile.BankDetails.ifsc,
				isDefault: 1,
				isDeleted: 0
			}
		}
		console.log(final);
		try {
			let res = await AdminServices.completeprofile(final).then(res => res);
			if (res.type) {
				if (res.data.Result === 'success') {
					let res2 = await AdminServices.enableMf(user.email).then((res) => { return res })
					if (res2.data.Result === 'success') {
						toast.success("Mutual Funds Enabled")
						db.remove(db.model.clientData);
						db.remove(db.model.userData);
					} else {
						toast.error("Error")
					}
					//navigate
					if((userProfile.taxStatus === '1'?userProfile.GuardianPan.kycRegistered : userProfile.panData.kycRegistered)===1){
						props.history.push('/start-investing');
					}else{
						props.history.push('/register-KYC');
					}
				} else {
					console.log('ERR : 2', res.data);
					toast.error("Try Again...")
				}
			} else {
				console.log('ERR : 1', res.data);
				toast.error("Try Again...")
			}
		} catch (error) {
			console.log('ERR : c', error);
			toast.error("Try Again...")
		}
	};

	React.useEffect(() => {
		db.get(db.model.userData)
			.then((u) => {
				if (u) {
					setUserProfile(u);
					if (u.sign) {
						setImg(u.sign || '');
						setsignID(u.signID || '');
						u.sign.startsWith('data:')
							? setmodalType('sign')
							: setmodalType('pick');
					}
				}
			})
			.then(() => {
				db.get(db.model.user).then((p) => {
					setUser(p.userData);
					getClientData(p.userData.id)
					setloading(false);
				});
			});
	}, []);

	if (loading) {
		return (
			<ActivityLoaderMintbox />
		);
	} else {

		return (
			<div className='mainContainer'>
				<MainHeader
					header="Activate mutual funds"
					navigation={props.history}
				/>
				<ProgressBar now={100} className="progress-bar-custom" />

				<div className="dataContainer" style={{paddingTop:'42px'}}>
					<div>
						{modalType === 'sign' ? (
							<>
								<div className="header1dark" style={{ marginBottom: "6%" }}>Sign in the box below</div>
								<div
									style={{
										border: "1px dashed "+cssVar.getPropertyValue('--light'),
										borderRadius: '5px',
										width: window.innerWidth - 33
									}}>
									<ReactSignatureCanvas ref={(ref) => {
										sigCanvas = ref
									}} penColor={cssVar.getPropertyValue("--black")}
										canvasProps={{ width: window.innerWidth - 33, height: 220, className: 'sigCanvas' }} />
									<div
										className='header1dark'
										style={
											{
												position: 'absolute',
												top: '230px',
												opacity: 0.5,
												textAlign: 'center',
												width: window.innerWidth - 33
											}
										}>
										Sign here
									</div>
									<div style={
										{
											position: 'absolute',
											top: '340px',
											textAlign: 'center',
											width: window.innerWidth - 33
										}
									}>
										<button style={{ backgroundColor: cssVar.getPropertyValue("--primaryGreen"), color: cssVar.getPropertyValue("--darkGreen"),border:"none",borderRadius:"6px",height:"44px",width:"80px",fontSize:"14px" }} onClick={() => handleSignature()} className="mx-2">Save</button>
										<button style={{ backgroundColor: cssVar.getPropertyValue("--primaryGreen"), color: cssVar.getPropertyValue("--darkGreen"),border:"none",borderRadius:"6px",height:"44px",width:"80px",fontSize:"14px"  }} onClick={() => handleClear()} className="mx-2">Clear</button>
									</div>
								</div>
							</>
						) :
							(
								<div >
									<div className="header1dark" style={{ marginBottom: "6%" }}>
										Upload signature
									</div>
									{imgSign === '' || imgSign === null ? (

										<div
											style={{
												borderWidth: 1,
												borderStyle: 'dashed',
												borderColor: cssVar.getPropertyValue("--secondaryGreen"),
												borderRadius: 10,
												backgroundColor: cssVar.getPropertyValue("--cardColor"),
												alignItems: 'center',
												padding: "30px",
												display: "flex",
												justifyContent: "space-around",
												marginTop: 20
											}}
											onClick={() => {
												inputFile.current.click();
											}}>
											<>
												<input type='file' accept="image/*" id='panfile' ref={inputFile} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

												<span className='bodyTextLight'
													style={{
														color: cssVar.getPropertyValue("--secondaryGreen")
													}}>
													Take a photo or upload an image
												</span>
												<img
													style={{
														color: cssVar.getPropertyValue("--secondaryGreen")
													}}
													src={Cloud} />
											</>
										</div>
									) : (
										<div
											style={{
												borderWidth: 1,
												borderStyle: 'dashed',
												borderColor: cssVar.getPropertyValue("--secondaryGreen"),
												borderRadius: 10,
												backgroundColor: cssVar.getPropertyValue("--cardColor"),
												display: "flex",
												flexDirection: "column",
												justifyContent: "space-around",
												height: "10px !important",


											}}>
											<img
												width="100%" height="160"

												style={{
													borderRadius: 10
												}}
												src={imgSign}

											/>
											<div
												style={{
													alignItems: 'center',
													padding: "10px",
													display: "flex",
													justifyContent: "space-around"
												}}
												onClick={() => {
													inputFile.current.click();
													setmodalType('pan');
												}}>
												<>
													<input type='file' accept="image/*" id='panfile' ref={inputFile} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

													<span className='bodyTextLight'
														style={{
															color: cssVar.getPropertyValue("--secondaryGreen")
														}}>
														Upload a different image
													</span>
													<img
														style={{color: cssVar.getPropertyValue("--secondaryGreen")}}
														resizeMode="contain"
														src={Cloud} />
												</>
											</div>
										</div>
									)}
								</div>)}


						{modalType === 'sign' ? (
							<p className="header2dark mt-3"
								style={{ color: cssVar.getPropertyValue("--secondaryGreen") }}

								onClick={() => {
									setmodalType('pick');
									setImg('');
									setsignID('')
								}}
							>
								Upload signature instead <AiOutlineRight size={12}/>
							</p>
						) : (
							<p className="header2dark mt-3"
								style={{ color: cssVar.getPropertyValue("--secondaryGreen") }}
								onClick={() => {
									setmodalType('sign');
									setImg('');
									setsignID('')

								}}
							>
								Sign it instead <AiOutlineRight size={12}/>
							</p>
						)}
					</div>
				</div>
				<div className="footer">
					<button
						className='solidButton'
						style={{opacity: nextDisabled ? 0.4 : 1}}
						onClick={!nextDisabled
							? () => onNextPress()
							: () => { }}
					>
						Activate mutual funds now
					</button>
				</div>
			</div>
		);


	}
};

export default SignUpload;
